.see-more-container button {
  width: 100%;
  margin: 0 auto;
  margin-top: 150px;
  margin-bottom: 50px;
  font-size: 1.5em;
  font-family: body-content;
}

@media screen and (max-width: 640px) {
  .see-more-container button {
    margin-top: 50px;
    margin-bottom: 0;
  }
}



